import React from 'react';
import { Switch, Route } from 'react-router-dom';

// Templates
import Error from '../components/PageTemplates/Error'
import PageTemplate from '../components/PageTemplates/PageTemplate';
import PageSideNavTemplate from '../components/PageTemplates/PageSideNavTemplate'

//Pages
import HomePage from '../components/PageTemplates/Pages/Home';
import AdminPage from '../components/Admin/Admin'
import ComponentsPage from '../components/PageTemplates/Pages/Components';
import StyleRefPage from '../components/PageTemplates/Pages/StyleRef';
import ColorsPage from '../components/PageTemplates/Pages/Colors';
import LoginPage from '../components/Auth/Login'

// Containers
import AuthContainer from '../Containers/Auth/Auth';
import AdminContainer from '../Containers/Admin';

const Routes = () => (
    <Switch>
        <Route
            exact
            path="/"
            render={props => (
                <PageTemplate>
                    <HomePage {...props}/>
                </PageTemplate>
            )}
        />
        <Route
            exact
            path="/components"
            render={props => (
                <PageTemplate>
                    <ComponentsPage {...props}/>
                </PageTemplate>
            )}
        />
        <Route
            exact
            path="/style"
            render={props => (
                <PageTemplate>
                    <StyleRefPage {...props}/>
                </PageTemplate>
            )}
        />
        <Route
            exact
            path="/colors"
            render={props => (
                <PageTemplate>
                    <ColorsPage {...props}/>
                </PageTemplate>
            )}
        />
        <Route
            exact
            path="/dashboard"
            render={props => (
                <PageSideNavTemplate>
                    <AdminContainer {...props} Layout={AdminPage}/>
                </PageSideNavTemplate>
            )}
        />
          <Route
            exact
            path="/login"
            render={props => (
                <PageTemplate>
                   <AuthContainer {...props} Layout={LoginPage}/>
                </PageTemplate>
            )}
        />
        <Route
            render={props => (
                <PageTemplate>
                    <Error {...props}  />
                </PageTemplate>
            )}
        />
    </Switch>
);

export default Routes;

